@import url(https://fonts.googleapis.com/css2?family=Bai+Jamjuree:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: "Bai Jamjuree", sans-serif;
  margin: 0;
}
h1 {
  font-size: 45px;
}
h2,
h3 {
  font-weight: bold;
}
p {
  color: hsl(201, 11%, 66%);
  font-size: 18px;
}

.Topimg {
  background-image: url(/Clipboard-Landing-WebApp/static/media/bg-header-desktop.3d39ff8e.png);
  background-repeat: no-repeat;
  background-size: 100% 15%;
  text-align: center;
}

@media (max-width: 1028px) {
  .Topimg {
    background-image: url(/Clipboard-Landing-WebApp/static/media/bg-header-mobile.611a5565.png);
    background-repeat: no-repeat;
    background-size: 100% 15%;
  }
}

.Intro {
  margin: 0 50px;
}
.Intro img {
  margin: 150px auto 25px;
}
.Intro h1 {
  font-size: 35px;
}

.Desktop {
  margin: 200px auto;
}
.HeaderLayout {
  margin: 0 250px;
}
.DesktopLayout {
  display: flex;
  margin: 100px auto;
  text-align: left;
}
.DesktopLayout img {
  position: relative;
  left: -50px;
  width: 50%;
}
.DesktopLayout div {
  margin: 25px 10px;
}

@media (max-width: 1028px) {
  .Desktop img {
    position: static;
    width: 75%;
  }
  .HeaderLayout {
    margin: 0 25px;
  }
  .DesktopLayout {
    display: block;
    text-align: center;
  }
}

.Access {
  margin: 100px 25px;
}
.Access img {
  margin: 50px auto;
  width: 65%;
}

.Workflow {
  margin: 100px 25px;
}
.Workflow p {
  margin-bottom: 50px;
}
.WorkflowLayout {
  display: flex;
  justify-content: space-evenly;
}
.WorkflowDescription {
  margin: 50px;
  width: 500px;
}

@media (min-width: 260px) and (max-width: 1280px) {
  .WorkflowLayout {
    display: block;
  }
  .WorkflowDescription {
    width: auto;
  }
}

.Testimonials {
  display: flex;
  justify-content: space-evenly;
  margin: 100px 25px;
}
.TestimonialsImg img {
  margin: 25px;
}
@media (min-width: 260px) and (max-width: 1280px) {
  .Testimonials {
    display: block;
  }
}

.Downloads {
  margin: 100px 25px;
}

.Footer {
  background-color: hsl(0, 0%, 89%);
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 25px;
  text-align: left;
}
.Footer img {
  width: 50px;
  height: 50px;
}
.FooterLogo img {
  cursor: pointer;
  margin: 5px;
  width: 25px;
  height: 25px;
}
.FooterLogo img:hover {
  -webkit-filter: invert(63%) sepia(15%) saturate(1859%) hue-rotate(122deg)
    brightness(95%) contrast(86%);
          filter: invert(63%) sepia(15%) saturate(1859%) hue-rotate(122deg)
    brightness(95%) contrast(86%);
}
.Footer p {
  color: black;
  cursor: pointer;
  font-size: 16px;
  margin: 25px;
}
.Footer p:hover {
  color: hsl(171, 66%, 44%);
}

@media (min-width: 260px) and (max-width: 1280px) {
  .Footer {
    display: block;
    text-align: center;
  }
}

